import { log } from "../../../../shared/support/log";
import { durationStore } from "../../store/duration-store";
import { currentTimeVPAID } from "../../store/time-store";
import type { VPAID } from "../../vpaid";
import { subscribeToVideo } from "./subscribe-to-video";

let isSetOnceActions = false;
let durationSetled = false;
let isImpressed = false;
/**
 * Отвечает за трекинг квартилей
 * Срабатывает на каждый timeupdate video
 */
export const timeUpdateHandler = (context: VPAID) => (): void => {
  if (!isImpressed) {
    context.tracker?.trackImpression();
    isImpressed = true;
  }
  /**
   * После того как трекнулся 100 квартиль уже ничего не делать
   */
  if (context.lastQuartileIndex >= context.quartileEvents.length) {
    return;
  }

  const video = context.videoSlot as HTMLVideoElement;

  /**
   * Обновить стор SVELTE
   */
  currentTimeVPAID.set(video.currentTime);
  durationStore.set(video.duration);
  /**
   * Если мы приняли VAST а не ролик сразу,
   * то трекать его события
   */
  if (context.tracker) {
    context.tracker.setProgress(video.currentTime);

    if (!durationSetled && !Number.isNaN(video.duration)) {
      durationSetled = true;
      // Для корректного трекинга квартилей сказать про продолжительность ролика
      log(`ADS DURATION = ${video.duration}`);

      context.tracker.setDuration(video.duration);
    }
  }

  if (!isSetOnceActions) {
    isSetOnceActions = true;
    subscribeToVideo(context);
  }

  const percentPlayed = (video.currentTime * 100.0) / video.duration;

  /**
   * Если процент больше или равен граничному значению очередного квартиля
   */
  if (
    percentPlayed >= context.quartileEvents[context.lastQuartileIndex]?.value
  ) {
    const lastQuartileEvent =
      context.quartileEvents[context.lastQuartileIndex].event;
    /**
     * Трекаем события квартиля
     */
    log(`VPAID COOKIE TRACK ${lastQuartileEvent}`);

    const callback = context.eventsCallbacks[lastQuartileEvent];

    if (video.currentTime === video.duration && context.tracker) {
      context.tracker.complete();
    }
    if (typeof callback === "function") {
      callback();
    }
    /**
     * Ожидаем следующий квартиль
     */
    context.lastQuartileIndex += 1;
  }
};
