import type { VPAID } from '../../vpaid';

/**
 * Хелпер для обновления размера плеера
 */
export const updateVideoPlayerSize = (context: VPAID) => () => {
  (context.videoSlot as HTMLVideoElement).setAttribute(
    'width',
    `${context.attributes.width}px`,
  );
  (context.videoSlot as HTMLVideoElement).setAttribute(
    'height',
    `${context.attributes.height}px`,
  );
};
