import { PixelError } from "../../../shared/errors/pixel-error";
import type { customAdParamsVpaidI } from "../../../shared/types/vast/vast-ad";
import type { VPAID } from "../vpaid";

const PROMISE_TIME_OUT = 1500;
/**
 * Матчит пиксели с DSP
 * Таймаут на матчинг 1500 с
 * В случае ошибки матчинга, должен трекнуть ссылку
 * из параметра errorUrl
 */
export const matchUser = async (context: VPAID) => {
  const { pixels } = context.parameters as customAdParamsVpaidI;

  /**
   * Если пикселей нет, просто не матчимся
   */
  if (!pixels) {
    return Promise.resolve();
  }

  return new Promise((resolveGlobal) => {
    Promise.allSettled(
      pixels?.map(({ pixel }) => {
        console.log(pixel);

        /**
         * На каждый пискель есть 1500 мс на разрешение,
         * иначе трекаем его error url
         */
        return Promise.race([
          fetch(pixel, { mode: "no-cors", credentials: "include" }),
          new Promise((_resolve, reject) => {
            setTimeout(() => {
              /**
               * Ошибка матчинга с реклами
               */
              reject(new PixelError());
            }, PROMISE_TIME_OUT);
          }),
        ]);
      })
    ).then(async (result) => {
      console.log(result);
      await Promise.all(
        result.map(async (linkResult, index) => {
          if (linkResult?.status === "rejected") {
            console.log(`Try to fetch ERROR ${pixels[index].error}`);
            return fetch(pixels[index].error, {
              mode: "no-cors",
              credentials: "include",
            }).catch((error) => console.log(error));
          }
          return Promise.resolve();
        })
      );
      resolveGlobal("");
    });
  });
};
