import type { VPAID } from "../../vpaid";
import { callEvent } from "../support/call-event";
import { removeAll } from "../support/remove-all";

/**
 * Called by the wrapper to stop the ad.
 */
export const stopAd = (context: VPAID) => (): void => {
  /* istanbul ignore if */
  if (context.isDestroyed) return;
  /**
   * Calling AdStopped immediately terminates the ad.
   * Setting a timeout allows events to go through.
   */
  const callback = callEvent(context);

  setTimeout(() => {
    callback("AdStopped");
    removeAll(context)();
  }, 250);
};
