import type { VPAID } from '../../vpaid';
import { callEvent } from '../support/call-event';
import { updateVideoPlayerSize } from '../support/update-video-player-size';
import type { viewModeT } from './init-ad';

/**
 * Изменяет размеры плеера
 */
export const resizeAd = (context: VPAID) => (width: number, height: number, viewMode: viewModeT): void => {
  context.attributes.width = width;
  context.attributes.height = height;
  context.attributes.viewMode = viewMode;
  updateVideoPlayerSize(context)();
  callEvent(context)('AdSizeChange');
};
