import type { VastCreativeLinear } from "vast-client";
import { VASTTracker as VastTracker } from "../../libs/vast-client.js";
import type { CurrentAds, setCurrentAdsI } from "../types/vast/current-ads.js";
import { VPAIDCondition } from "../vpaid/VPAID-condition.js";

/**
 * Декодирует Error url
 */
const decodeURLs = (decode: string[]) => {
  return decode.map((encoded) => decodeURI(encoded));
};

/**
 * Возвращает обьект
 * ads - где распарсенный васт
 * containVPAID - есть ли VPAID
 * tracker - интерфейс треккера
 */
export const setCurrentAds = (props: setCurrentAdsI): CurrentAds => {
  const currentAds = {} as CurrentAds;
  currentAds.ads = props.restAds.ads[0];
  currentAds.ads.errorURLTemplates = decodeURLs(
    currentAds.ads.errorURLTemplates
  );

  currentAds.containVPAID = (
    currentAds.ads.creatives[0] as VastCreativeLinear
  ).mediaFiles.some(VPAIDCondition);

  currentAds.tracker = new VastTracker(
    props.vastClient,
    currentAds.ads,
    currentAds.ads.creatives[0] as VastCreativeLinear
  );

  currentAds.vastClient = props.vastClient;
  return currentAds;
};
