import type { VPAID } from "../../vpaid";
import { updateVideoPlayerSize } from "./update-video-player-size";
/**
 * Если не было тега видео то создает его
 * Обновляет размеры плеера на переданные при initAd
 * Проставляет src
 */
export const updateVideoSlot = (context: VPAID) => () => {
  /**
   * Если видео тег не был передан
   * создать его и добавить к slot
   */
  if (context.videoSlot == null) {
    context.videoSlot = document.createElement("video");
    (context.slot as HTMLDivElement).appendChild(context.videoSlot);
  }
  /**
   * For In Stream videos
   */
  context.videoSlot.muted = false;
  context.videoSlot.autoplay = true;
  context.videoSlot.playsInline = true;
  context.videoSlot.setAttribute("x-webkit-airplay", "allow");
  context.videoSlot.setAttribute("webkit-playsinline", "true");

  updateVideoPlayerSize(context)();

  if (context.parameters?.videos) {
    const videos = context.parameters.videos;

    for (let i = 0; i < videos.length; i++) {
      // Choose the first video with a supported mimetype.
      if (context.videoSlot.canPlayType(videos[i]?.mimetype as string) != "") {
        context.videoSlot.setAttribute("src", videos[i].url);
        break;
      }
    }
  }
};
