<script lang="ts">
  import { onDestroy, onMount } from "svelte";
  import type { VastCreativeLinear } from "vast-client";
  import { domainCutter } from "../../../shared/support/domain-cutter";
  import { isTouchDevice } from "../../../shared/support/is-touch-device";
  import type { CurrentAds } from "../../../shared/types/vast/current-ads";
  import { clickAction } from "../methods/support/click-action";
  import { controlleStore } from "../store/controlle-store";
  import type { VPAID } from "../vpaid";

  export let currentAds = {} as CurrentAds;
  export let context = {} as VPAID;

  const isEngLang = /en/g.test(navigator.language);

  const creative = currentAds?.ads?.creatives[0] as VastCreativeLinear;

  const isTouchDeviceConst = isTouchDevice();
  let opacity = isTouchDeviceConst ? 0.5 : 0;

  const display = () => {
    opacity = 0.5;
  };
  const hide = () => {
    opacity = 0;
  };

  onMount(() => {
    if (!isTouchDeviceConst) {
      context.slot?.addEventListener("mouseenter", display);
      context.slot?.addEventListener("mouseleave", hide);
    }
  });
  onDestroy(() => {
    if (!isTouchDeviceConst) {
      context.slot?.removeEventListener("mouseenter", display);
      context.slot?.removeEventListener("mouseleave", hide);
    }
  });
  const clickUrl = creative.videoClickThroughURLTemplate;
  const domain = domainCutter(String(clickUrl?.url), true) || "";
  const goLabel = isEngLang ? "Go" : "Перейти";
</script>

{#if $controlleStore.fullScreenClick}
  <div
    title="{goLabel}"
    style="opacity: {opacity};"
    class="w"
    on:click="{() => {
      clickAction(context);
    }}"
  >
    <div class="d">
      {goLabel}
      {domain}
    </div>
  </div>
{/if}

