<script lang="ts">
  import type { VastCreativeLinear } from "vast-client";
  import type { CurrentAds } from "../../../shared/types/vast/current-ads";
  import { controlleStore } from "../store/controlle-store";

  export let currentAds = {} as CurrentAds;

  const iconUrl = (currentAds.ads?.creatives[0] as VastCreativeLinear)?.icons[0]
    ?.staticResource;
  const descriptionText = currentAds.ads?.description;
  /**
   * Если нет дескрипшена и иконки, то крестик смещается наверх
   */
  let isShortHeader = !iconUrl && !descriptionText;
</script>

