  {#if !isShortHeader}<div class="gradient"></div>{/if}
  <div class="header">
    {#if !isShortHeader}
      <div class="{iconUrl ? 'description--full' : 'description--short'}">
        {#if iconUrl}
          <img class="description-icon" src="{iconUrl}" alt="ads-logo" />
        {/if}
        {#if descriptionText}
          <div class="description-text">
            {descriptionText}
          </div>
        {/if}
      </div>
    {/if}
  </div>
{/if}

