import type { VPAID } from '../../vpaid';
import type { vpaidEventTypeT } from '../../vpaid-types';

/**
 * Registers a callback for an event.
 * @param {Function} aCallback The callback function.
 * @param {string} eventName The callback type.
 * @param {Object} aContext The context for the callback.
 */
export const subscribe = (context: VPAID) => (
  aCallback: () => void,
  eventName: keyof vpaidEventTypeT,
  aContext: unknown,
): void => {
  const callBack = aCallback.bind(aContext);
  context.eventsCallbacks[eventName] = callBack;
};
