import { VPAID } from "./vpaid";

/**
 * определяем getVPAIDAd в window
 * в соответствии со стандартом
 */
declare global {
  interface Window {
    getVPAIDAd: () => VPAID;
  }
}

/**
 * Main function called by wrapper to get the vpaid ad.
 * @return {Object} The vpaid compliant ad.
 */
window.getVPAIDAd = function (): VPAID {
  /**
   * Вызываться будет в контексте фрейма, по стандарту
   * frameContext?.getVPAIDAd();
   * Возвращает adUnit
   */
  return new VPAID();
};
