import type { VPAID } from "../../vpaid";
import type { vpaidEventTypeT } from "../../vpaid-types";
import { log } from "./../../../../shared/support/log";

/**
 * Вызывает коллбек от кода который вызвал VPAID
 */
export const callEvent =
  (context: VPAID) =>
  (eventType: keyof vpaidEventTypeT, message?: string): void => {
    if (eventType in context.eventsCallbacks) {
      const callback = context.eventsCallbacks[eventType];
      log(eventType);
      if (typeof callback === "function") {
        message ? callback(message) : callback("");
      }
    }
  };
