import type { VPAID } from "../../vpaid";
import { callEvent } from "../support/call-event";
import { removeAll } from "../support/remove-all";

/**
 * Skips the ad.
 */
export const skipAd = (context: VPAID) => (): void => {
  /* istanbul ignore if */
  if (context.isDestroyed) return;

  const { tracker } = context;
  if (tracker) tracker.skip();

  setTimeout(() => {
    callEvent(context)("AdSkipped");
    removeAll(context)();
  }, 250);
};
