import type { VPAID } from '../../vpaid';
import type { vpaidEventTypeT } from '../../vpaid-types';

/**
 * Removes a callback based on the eventName.
 *
 * @param {string} eventName The callback type.
 */
export const unsubscribe = (context: VPAID) => (eventName: keyof vpaidEventTypeT): void => {
  context.eventsCallbacks[eventName] = () => void 0;
};
