import { controlleStore } from "../../store/controlle-store";
import type { UiElements } from "./../../methods/support/draw-ui";

/**
 * It sets the target UI elements to the store.
 * @param {UiElements} ui - UiElements
 */
export const setTargetUI = (ui: UiElements) => {
  controlleStore.set({
    actionButton: !!ui.actionButton,
    header: !!ui.header,
    fullScreenClick: !!ui.fullScreenClick,
    transparentClick: !!ui.transparentClick,
    skip: !!ui.skip,
    tel: ui.tel,
  });
};
