import type { VASTClient } from "vast-client";

/**
 * Заменяет макросы.
 * В частности подставляет высоту и ширину инициализированного плеера
 *
 * Эти макросы работают не только в VAST URL а вообще в любых
 * которые обрабатывает плеер
 *
 * Работает через добавление кастомной функции в vastParser.addURLTemplateFilter
 */
export const replaceMacro = (
  vastClient: VASTClient,
  widthAndHeight: [number, number]
) => {
  const vastParser = vastClient.getParser();
  vastParser.addURLTemplateFilter((vastUrl: string) => {
    return vastUrl
      .replace("%5Bwidth%5D", `${widthAndHeight[0]}`)
      .replace("%5Bheight%5D", `${widthAndHeight[1]}`)
      .replace("%5Bpage_url%5D", `${document.location.href}`)
      .replace(
        "%5Brandom%5D",
        `${Math.floor(Math.random() * (100000000 - 1) + 1)}`
      );
  });
};
