import type { VastAd, VASTClient, VASTTracker } from "vast-client";
import ActionSvelte from "../../ui/action.svelte";
import FullScreenClickSvelte from "../../ui/full-screen-click.svelte";
import HeaderSvelte from "../../ui/header.svelte";
import MobileAction from "../../ui/mobile-action.svelte";
import MobileSkip from "../../ui/mobile-skip.svelte";
import Skip from "../../ui/skip.svelte";
import Tel from "../../ui/tel.svelte";
import TransparentClickSvelte from "../../ui/transparent-click.svelte";
import type { VPAID } from "../../vpaid";

export interface UiElements {
  /**
   * Кнопка с доменом при клике по котрой переход
   */
  actionButton?: boolean;
  /**
   * Шапка с описанием и иконкой
   */
  header?: boolean;
  /**
   * Навешивает клик на всю область видео
   * при наведении пишет подсказку что будет переход
   * при клике
   */
  fullScreenClick?: boolean;
  /**
   * Навешивает клик по всей области без появляющегося описания
   */
  transparentClick?: boolean;
  /**
   * Кнока скипа
   */
  skip?: boolean;
  /**
   * Кнока скипа mobile
   */
  mobileSkip?: boolean;
  /**
   * Кнока перехода мобильная
   */
  mobileAction?: boolean;
  /**
   * Кнопка с телефоном.
   * На мобильном вызывает действие звонка
   */
  tel?: string | number | boolean;
  /**
   * Отображаем ли на мобильном устройстве
   */
  isMobile: boolean;
}
/**
 * Рисует UI эллементы
 *
 * Они могут быть отключены по команде AdLog от нижележащего VPAID
 * формат для отключения всего UI
 * {
 *  showUI: false
 * }
 */
export const drawUi = (context: VPAID) => {
  /**
   * UI elements
   */

  new ActionSvelte({
    target: context.slot as HTMLElement,
    props: {
      currentAds: {
        ads: context.ads as VastAd,
        containVPAID: true,
        tracker: context.tracker as VASTTracker,
        vastClient: context.vastClient as VASTClient,
      },
      context,
      player: context.slot as HTMLElement,
    },
  });

  new MobileAction({
    target: context.slot as HTMLElement,
    props: {
      currentAds: {
        ads: context.ads as VastAd,
        containVPAID: true,
        tracker: context.tracker as VASTTracker,
        vastClient: context.vastClient as VASTClient,
      },
      context,
      player: context.slot as HTMLElement,
    },
  });

  new HeaderSvelte({
    target: context.slot as HTMLElement,
    props: {
      currentAds: {
        ads: context.ads as VastAd,
        containVPAID: true,
        tracker: context.tracker as VASTTracker,
        vastClient: context.vastClient as VASTClient,
      },
    },
  });

  new FullScreenClickSvelte({
    target: context.slot as HTMLElement,
    props: {
      currentAds: {
        ads: context.ads as VastAd,
        containVPAID: true,
        tracker: context.tracker as VASTTracker,
        vastClient: context.vastClient as VASTClient,
      },
      context,
    },
  });

  new TransparentClickSvelte({
    target: context.slot as HTMLElement,
    props: {
      context,
    },
  });

  new Skip({
    target: context.slot as HTMLElement,
    props: {
      currentAds: {
        ads: context.ads as VastAd,
        containVPAID: true,
        tracker: context.tracker as VASTTracker,
        vastClient: context.vastClient as VASTClient,
      },
      context,
    },
  });

  new MobileSkip({
    target: context.slot as HTMLElement,
    props: {
      currentAds: {
        ads: context.ads as VastAd,
        containVPAID: true,
        tracker: context.tracker as VASTTracker,
        vastClient: context.vastClient as VASTClient,
      },
      context,
    },
  });

  new Tel({
    target: context.slot as HTMLElement,
    props: {
      context,
    },
  });
};
