import type { VPAID } from "../../vpaid";
import { callEvent } from "../support/call-event";
import { overlayOnClick } from "../support/overlay-on-click";

/**
 * Called by the wrapper to start the ad.
 */
export const startAd = (context: VPAID) => (): void => {
  const video = context.videoSlot as HTMLVideoElement;
  console.log("startAd event");
  const isPlaying =
    !video.paused && !video.ended && video.readyState > video.HAVE_CURRENT_DATA;

  console.log(`isPlaying = ${isPlaying}`);

  if (!isPlaying) {
    (context.videoSlot as HTMLVideoElement)
      .play()
      .then(() => {
        const img = document.createElement("img");
        img.src = context.parameters.overlay || "";
        (context.slot as HTMLDivElement).appendChild(img);

        img.addEventListener("click", overlayOnClick(context), false);

        // add a test mute button
        /*   const muteButton = document.createElement("input");
    muteButton.setAttribute("type", "button");
    muteButton.setAttribute("value", "mute/unMute");

    muteButton.addEventListener(
      "click",
      muteButtonOnClick(context),
      false
    );
    (context.slot as HTMLDivElement).appendChild(muteButton); */
        callEvent(context)("AdImpression");
        callEvent(context)("AdStarted");
      })
      .catch((error) => {
        (context.videoSlot as HTMLVideoElement).muted = true;
        /**
         * Try again
         */
        context.videoSlot?.play();

        console.log(error);
      });
  } else {
    callEvent(context)("AdStarted");
  }
};
