<script lang="ts">
  import { onDestroy } from "svelte";
  import type { VastCreativeLinear } from "vast-client";
  import type { CurrentAds } from "../../../shared/types/vast/current-ads";
  import { SKIP_DEFAULT_TIMEOUT } from "../constants";
  import { skipAd } from "../methods/standart-vpaid/skip-ad";
  import { controlleStore } from "../store/controlle-store";
  import { durationStore } from "../store/duration-store";
  import { currentTimeVPAID } from "../store/time-store";
  import type { VPAID } from "../vpaid";

  export let currentAds: CurrentAds;
  export let context = {} as VPAID;

  const isEngLang = /en/g.test(navigator.language);

  const creative = currentAds.ads.creatives[0] as VastCreativeLinear;

  $: duration = Number(context.videoSlot?.duration);
  /**
   * В любом случае будет скип через время указанное
   * в константе SKIP_DEFAULT_TIMEOUT
   */
  const skipTime = creative.skipDelay
    ? Number(creative.skipDelay) > 30
      ? SKIP_DEFAULT_TIMEOUT
      : creative.skipDelay
    : SKIP_DEFAULT_TIMEOUT;

  const isSkipInVast = Number(creative.skipDelay) > 0;
  $: isCreativLong = duration > SKIP_DEFAULT_TIMEOUT;
  $: isDrawSkipCondition = isSkipInVast || isCreativLong;
  $: notDrawCondition = skipTime >= duration;
  $: skipCoundown = skipTime;

  let unsubscribeFromTimeStore = currentTimeVPAID.subscribe((progressTime) => {
    if (progressTime > skipTime) {
      skipCoundown = 0;
    } else {
      skipCoundown = skipTime - Math.floor(progressTime);
    }
  });
  let unsubscribeFromDurationStore = durationStore.subscribe(
    (durationValue) => {
      duration = durationValue;
    }
  );

  onDestroy(() => {
    unsubscribeFromTimeStore();
    unsubscribeFromDurationStore();
  });
</script>

{#if $controlleStore.skip && isDrawSkipCondition && !notDrawCondition}
  <button
    class="skip"
    style="width: {skipCoundown !== 0} 234px : 0px"
    disabled="{skipCoundown !== 0}"
    on:click="{() => {
      skipAd(context)();
    }}"
  >
    <div>{isEngLang ? "Skip" : "Пропустить рекламу "}</div>
    {#if skipCoundown > 0.3}
      <span style="width: 40px;">{skipCoundown}</span>
    {:else}
      <svg class="icon" version="1.1" viewBox="0 0 36 36"
        ><path d="M 12,24 20.5,18 12,12 V 24 z M 22,12 v 12 h 2 V 12 h -2 z"
        ></path></svg
      >
    {/if}
  </button>
{/if}

<style>
  .icon {
    z-index: 9992;
    width: 36px;
    height: auto;
    fill: #fff;
  }
  .skip {
    text-shadow: 0 0 4px rgb(0 0 0 / 75%);
    color: #fff;
    z-index: 9992;
    position: absolute;
    fill: rgb(46, 46, 46);
    direction: ltr;
    cursor: pointer;
    padding: 10px 6px 8px 10px;
    outline: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40px;
    font-size: 18px;
    opacity: 0.5;
    bottom: 28px;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-right: 0;
    min-width: 0;
    max-width: 45%;
  }
  .skip:hover {
    background: rgba(0, 0, 0, 0.9);
    color: #fff;
    opacity: 1;
  }
  @media (min-width: 320px) and (max-width: 767px) {
    .skip {
      font-size: 1em;
    }
  }
</style>
