import type { VastMediaFile } from "vast-client";

/**
 * Проверяет mimeType и apiFramework на соответствие требованиям для VPAID
 * @param item
 */
export const VPAIDCondition = (item: VastMediaFile): boolean => {
  return (
    item.apiFramework === "VPAID" && item.mimeType === "application/javascript"
  );
};
