import type { UiElements } from "../../methods/support/draw-ui";
import { setPressetUI } from "./set-presset-ui";
import { setTargetUI } from "./set-target-ui";

export const setUI = (event: { showUI: UiElements | boolean }) => {
  const ui = event.showUI;

  if (typeof ui === "boolean") {
    return setPressetUI(ui);
  }

  if (typeof ui === "object") {
    return setTargetUI(ui);
  }
};
