import type { VastCreativeLinear } from 'vast-client';
import type { VPAID } from '../../vpaid';
import { callEvent } from './call-event';

export const clickAction = (context: VPAID) => {
  const { tracker } = context;
  const clickUrl = String(
    (context.ads?.creatives[0] as VastCreativeLinear)
      ?.videoClickThroughURLTemplate?.url,
  );
  if (tracker) {
    tracker.click();
  }
  window.open(clickUrl);
  callEvent(context)('AdClickThru');
};
