import type { VastAd, VastCreativeLinear } from "vast-client";
import { checkIsMobile } from "../../../../shared/support/detects/check-is-mobile";
import { log } from "../../../../shared/support/log";
import { controlleStore } from "../../store/controlle-store";
import type { VPAID } from "../../vpaid";
import VpaidSvelte from "../../VPAID/vpaid-svelte.svelte";
import { drawUi } from "./draw-ui";
import { updateVideoSlot } from "./update-video-slot";

/**
 * Инициализирует VPAID и прямою рекламу
 */
export const initAds = (context: VPAID) => {
  /**
   * Если приняли чужой VPAID
   */
  if (context.containVPAID) {
    log("DETECT VPAID");

    /**
     * Создать видео элемент если он не был передан
     * В случае васта там проставятся еще и source для него
     */
    updateVideoSlot(context)();

    new VpaidSvelte({
      target: context.slot as HTMLElement,
      props: {
        param: {
          isMuted: false,
          currentAds: {
            ads: context.ads as VastAd,
            containVPAID: true,
            tracker: context.tracker,
            vastClient: context.vastClient,
          },
          config: { aspect_ratio: 0.5625, looped: false },
          widthAndHeight: [context.attributes.width, context.attributes.height],
          video: context.videoSlot as HTMLVideoElement,
          wrapper: context.slot as HTMLDivElement,
          container: context.slot as HTMLDivElement,
          context,
        },
      },
    });
  } else {
    const isMobileDevice = !!checkIsMobile();

    controlleStore.set({
      actionButton: false,
      header: true,
      fullScreenClick: false,
      transparentClick: !isMobileDevice,
      skip: !isMobileDevice,
      mobileSkip: isMobileDevice,
      mobileAction: isMobileDevice,
      tel: false,
      isMobile: isMobileDevice,
    });
    /**
     * Проставили данные для видео
     * До этого момента дойдет только если был принят VAST
     * с видео
     */
    if (context.ads.creatives) {
      context.parameters.videos = (
        context.ads.creatives as VastCreativeLinear[]
      )[0].mediaFiles.map((file) => ({
        mimetype: file.mimeType || "",
        url: file.fileURL || "",
      }));
    }

    /**
     * Создать видео элемент если он не был передан
     * В случае васта там проставятся еще и source для него
     */
    updateVideoSlot(context)();
  }

  drawUi(context);
};
