import { errorCodesVAST } from "../../../../shared/errors/error-codes-vast";
import { log } from "../../../../shared/support/log";
import type { VPAID } from "../../vpaid";
import { callEvent } from "./call-event";
import { clickAction } from "./click-action";
import { removeAll } from "./remove-all";

let isImpressed = false;
/**
 * Подписывается на все события видео для правильного трекинга
 * ссылок в приянтом васте и передачи вверх событий VPAID
 */
export const subscribeToVideo = (context: VPAID) => {
  const video = context.videoSlot as HTMLVideoElement;
  /* Подписаться на все события video для правильного трекинга */

  /**
   * Отсылает событие в вышестоящий плеер
   */
  const send = callEvent(context);

  const pauseAction = () => {
    if (context.tracker) context.tracker.setPaused(true);
    send("AdPaused");
  };
  const endAction = () => {
    if (context.tracker) context.tracker.complete();

    setTimeout(() => {
      send("AdVideoComplete");
      removeAll(context)();
    }, 250);
  };
  const playAction = () => {
    if (context.tracker) {
      if (!isImpressed) {
        context.tracker.trackImpression();
        isImpressed = true;
      }
      context.tracker.setPaused(false);
    }
    if (!isImpressed) {
      send("AdImpression");
    }
    send("AdPlaying");
  };
  const errorAction = (error: ErrorEvent) => {
    if (context.tracker) {
      if (error instanceof Error) {
        log(error.message, true);
      }
      const errorCode =
        parseInt(error?.message) > 900
          ? error?.message
          : errorCodesVAST[error?.message as keyof typeof errorCodesVAST] ||
            "900";

      context.tracker.errorWithCode(errorCode, parseInt(errorCode) > 901);
    }
    send("AdError", "video error");
    removeAll(context)();
  };

  const volumeAction = (e: Event) => {
    const targetVideo = <HTMLVideoElement>e.target;

    if (context.tracker) {
      context.tracker.setMuted(targetVideo.muted);
    }

    context.attributes.volume = targetVideo.volume;
    send("AdVolumeChange");
  };

  video.addEventListener("play", playAction);
  video.addEventListener("pause", pauseAction);
  video.addEventListener("ended", endAction);
  video.addEventListener("error", errorAction);
  video.addEventListener("volumechange", volumeAction);
  /**
   * Отдаем на откуп эллментам UI управления
   * а не вешаем глобальный клик
   */
  video.addEventListener("click", () => clickAction(context));

  /* По сути отписываться не нужно, так как все это удалиться из DOM */
};
