import type { VASTClient } from "vast-client";
import { errorCodesVAST } from "../errors/error-codes-vast";
import { ReinitError } from "../errors/reinit-error";
import type { CurrentAds } from "../types/vast/current-ads";
import { setCurrentAds } from "./set-current-ads";

/**
 * В случае ошибки на разных этапах
 * попытаться получить следующую рекламную
 * кампанию из пода.
 *
 * Эта функция возвращается в fetchVast и
 * дергается логером
 *
 * Выбрасывает в случае ошибки ReinitError
 */
export const getNextAds = async (
  vastClient: VASTClient
): Promise<undefined | CurrentAds> => {
  if (vastClient.hasRemainingAds()) {
    try {
      const restAds = await vastClient.getNextAds();
      
      return setCurrentAds({ vastClient, restAds: restAds });
    } catch (error) {
      /**
       * Если в конкретной рекламной компании
       * возникнет ошибка то попробовать следующую,
       * при ее наличии или перезапустить плеер.
       */
      if (vastClient.hasRemainingAds()) {
        return getNextAds(vastClient);
      } else throw new ReinitError(errorCodesVAST["No active campaign"]);
    }
  } else {
    throw new ReinitError(errorCodesVAST["No active campaign"]);
  }
};
