import type { CurrentAds } from "../types/vast/current-ads";
import { VastCreativeLinear } from "../../libs/vast-client.js";

/**
 * Создает строку с контекстом VPAID iframe
 * для вставки в iframe через write
 */
export const createFrameScript = (currentAds: CurrentAds, cssId: string) => {
  return `<head><style>body{margin:0}</style></head><body><script type="text/javascript" id="${cssId}"  charset="UTF-8" src="${
    (currentAds.ads.creatives[0] as VastCreativeLinear)?.mediaFiles[0]?.fileURL
  }" async></script></body>`;
};
