import { log } from "../../../../shared/support/log";
import type { VPAID } from "../../vpaid";

/**
 * Удаляет VPAID из DOM
 */
export const removeAll = (context: VPAID) => (): void => {
  setTimeout(() => {
    log("Remove all action");
    if (context.slot !== null) {
      context.slot.innerHTML = "";
    }
    context.isDestroyed = true;
  }, 75);
};
