<script lang="ts">
  import { onDestroy, onMount } from "svelte";
  import type { VastCreativeLinear } from "vast-client";
  import { errorCodesVAST } from "../../../shared/errors/error-codes-vast";
  import { TerminateError } from "../../../shared/errors/terminate-error";
  import { log } from "../../../shared/support/log";
  import type { CurrentAds } from "../../../shared/types/vast/current-ads";
  import type { vpaidEvents } from "../../../shared/types/vpaid/vpaid-callbacks";
  import type { vpaidMethods } from "../../../shared/types/vpaid/vpaid-methods";
  import { bindToVastActions } from "../../../shared/vpaid/bind-actions";
  import { checkSpec } from "../../../shared/vpaid/check-spec";
  import { createFrameScript } from "../../../shared/vpaid/create-frame-script";
  import { watchForVpaidSize } from "../../../shared/vpaid/set-vpaid-height";
  import { callEvent } from "../methods/support/call-event";
  import { errorStore } from "../store/error-store";
  import type { VPAID } from "../vpaid";
  import type { configVpaidI, vpaidPropsI } from "../vpaid-types";
  import { listenEvents } from "../VPAID/on-load/listen-events";

  export let param: vpaidPropsI = {
    isMuted: false,
    currentAds: {} as CurrentAds,
    config: {} as configVpaidI,
    widthAndHeight: [0, 0],
    video: document.createElement("video"),
    wrapper: document.createElement("div"),
    container: document.createElement("div"),
    context: {} as VPAID,
  };

  let observerVpaidUnsub = () => {};
  /* DOM REFS */
  /**
   * Для доступа к DOM элементам из кода
   */
  let iframeVpaid: HTMLIFrameElement;
  let events: Partial<vpaidEvents>;
  let adUnit: vpaidMethods;
  /** To fix TS type error*/
  let eventName: keyof typeof events;
  /**
   * Инициализирует VPAID:
   * - Проверяет поддерживает ли он все методы которые
   * должен в соответствии со стандартом
   * - Обменивается рукопажатием
   * - Инициализирует через initAd
   * - Навешивает действия на VPAID events
   *
   * Возвращает функцию отписки от прослушивания событий VPAID
   */
  function onLoad() {
    adUnit = (
      iframeVpaid.contentWindow as unknown as { getVPAIDAd: () => vpaidMethods }
    ).getVPAIDAd();
    /**
     * Проверить поддерживает ли VPAID все дефолтные методы
     */
    if (!checkSpec(adUnit)) {
      errorStore.set(new TerminateError(errorCodesVAST["VPAID is invalid"]));
    }

    // Обмен версиями
    adUnit.handshakeVersion("2.0");

    // Инициализировать
    adUnit.initAd(
      param.widthAndHeight[0],
      param.widthAndHeight[0] * param.config.aspect_ratio,
      "normal",
      -1,
      {
        AdParameters:
          /**
           * For settings vpaid
           */
          (param.currentAds.ads.creatives[0] as VastCreativeLinear)
            .adParameters,

        /* For advert vpaid */
      },
      {
        slot: param.wrapper,
        videoSlot: param.video,
        videoSlotCanAutoPlay: true,
      }
    );

    observerVpaidUnsub = watchForVpaidSize({
      container: param.wrapper,
      ASPECT_RATIO: param.config.aspect_ratio,
      adUnit,
    });

    events = listenEvents({
      video: param.video,
      adUnit: adUnit,
      vastTracker: param.currentAds.tracker,
      looped: param.config.looped,
      context: param.context,
    });

    for (eventName in events) {
      adUnit.subscribe(events[eventName] as () => void, eventName);
    }

    bindToVastActions(adUnit, param.currentAds.tracker);
  }
  onMount(() => {
    const cssId = "df-vpaid";
    /* Контекст фрейма */
    const frameContext = iframeVpaid.contentWindow;
    /* Добавить во фрейм пустой документ со скриптом VPAID */

    frameContext?.document.write(createFrameScript(param.currentAds, cssId));
    /* Cсылка на скрипт внутри фрейма */
    const script = frameContext?.document.querySelector(`script`);

    script?.addEventListener("load", onLoad);
    script?.addEventListener("error", () =>
      errorStore.set(new TerminateError(errorCodesVAST["Can't load VPAID"]))
    );
    const is_safari = navigator.userAgent.indexOf("Safari") > -1;
    if (is_safari) {
      frameContext?.document.close();
    }

    errorStore.subscribe((errorInStore) => {
      /**
       * Null будет при обнулении стора, на него не нужно реакции
       */
      if (errorInStore === null) {
        return;
      }
      /**
       * Если пришел код, то отправить его, иначе получить код из описания
       */
      const errorCode =
        parseInt(String(errorInStore?.message)) > 900
          ? (errorInStore?.message as string)
          : errorCodesVAST[errorInStore?.message as keyof typeof errorCodesVAST] ||
            "900";

      /**
       * Ошибка трекается в случае аварийного завершения
       * и перезапуска плеера
       */
      const trackError = () => {
        callEvent(param.context)("AdError", "vpaid error");
        /** Трекнуть ошибку */
        param.currentAds.tracker.errorWithCode(
          errorCode,
          parseInt(errorCode as string) > 901 ? true : false
        );
      };

      if (errorInStore instanceof TerminateError) {
        log(errorInStore, true);
        log("TERMINATE PLAYER");

        trackError();
        try {
          param.wrapper?.parentNode?.removeChild(param.wrapper);
        } catch (removeChildError) {
          log(removeChildError, true);
        }
      }
    });
  });

  onDestroy(() => {
    /*     if (iframeVpaid) iframeVpaid.removeEventListener("load", onLoad);
    if (content) content.$destroy(); */
    // When user exits the page
    log("unsubscribe from VPAID events");
    param.currentAds.tracker.close();

    for (eventName in events) {
      adUnit.unsubscribe(events[eventName] as () => void, eventName);
    }

    observerVpaidUnsub();
  });
</script>

<iframe
  sandbox="allow-same-origin allow-scripts allow-popups"
  title="vpaid"
  class="iframe"
  bind:this="{iframeVpaid}"></iframe>

<style>
  .iframe {
    border: 0px;
    margin: 0px;
    opacity: 1;
    padding: 0px;
    height: 100%;
    position: relative;
    width: 100%;
    top: 0;
    left: 0;
  }
</style>
