import type { VastCreativeLinear, VastSystem } from "vast-client";
import { PixelError } from "../../../../shared/errors/pixel-error";
import { log } from "../../../../shared/support/log";
import type { customAdParamsVpaidI } from "../../../../shared/types/vast/vast-ad";
import { instanceOfCookieMatching } from "../../cookie-matching/guards/is-coockie-matching-params";
import { matchUser } from "../../cookie-matching/match-user";
import { fetchVast } from "../../VAST/fetch-vast";
import type { VPAID } from "../../vpaid";
import type { CurrentAds } from "./../../../../shared/types/vast/current-ads";

/**
 * Счетчик принятых VPAID
 */
let vpaidCointer = 0;
const DF_NETWORK_AD_SYSTEM = "Ad Server Digital Frontier";

export const fetchCreativeDataWithMatch = async (
  context: VPAID,
  creativeData: { AdParameters: string }
): Promise<void> => {
  if (vpaidCointer > 2) {
    throw new Error("to many vpaids");
  }
  context.parameters = JSON.parse(creativeData.AdParameters);

  /**
   * Если в AdParameters получили информацию для куки матчинга
   */
  if (instanceOfCookieMatching(context.parameters)) {
    let matchResult;
    try {
      matchResult = await matchUser(context);
    } catch (error) {
      if (matchResult instanceof PixelError) {
        log(matchResult.message, true);
      }
    }

    const curretnAds = (await fetchVast({
      url: context.parameters.vast_link,
      widthAndHeight: [context.attributes.width, context.attributes.height],
    })) as CurrentAds;

    const adSystem = (curretnAds.ads.system as VastSystem)?.value;

    /**
     * Если приняли еще раз свой VPAID
     */
    if (curretnAds.containVPAID && adSystem === DF_NETWORK_AD_SYSTEM) {
      log("Contain CUSTOM VPAID");
      context.parameters = (curretnAds.ads.creatives[0] as VastCreativeLinear)
        .adParameters as unknown as customAdParamsVpaidI;
      vpaidCointer += 1;

      await fetchCreativeDataWithMatch(context, {
        AdParameters:
          typeof context.parameters === "string"
            ? context.parameters
            : JSON.stringify(context.parameters),
      });
    }

    context.tracker = curretnAds.tracker;
    context.ads = curretnAds.ads;
    context.containVPAID = curretnAds.containVPAID;
  }
};
