<script lang="ts">
  import { onMount } from "svelte";
  import { isTouchDevice } from "../../../shared/support/is-touch-device";
  import { onlyNumbersFromString } from "../methods/support/only-numbers-from-string";
  import { controlleStore } from "../store/controlle-store";
  import type { VPAID } from "../vpaid";

  export let context = {} as VPAID;

  const isTouchDeviceConst = isTouchDevice();
  let opacity = isTouchDeviceConst ? 0.6 : 0.4;

  const telForHref = onlyNumbersFromString($controlleStore.tel);
  onMount(() => {
    if (!isTouchDeviceConst) {
      context.slot?.addEventListener("mouseenter", () => {
        opacity = 1;
      });
      context.slot?.addEventListener("mouseleave", () => {
        opacity = 0.4;
      });
    }
  });
</script>

{#if !!$controlleStore.tel}
  <button style="background: rgba(0, 0, 0, {opacity});" class="w">
    <a href="{`tel:+${telForHref}`}">{$controlleStore.tel}</a>
  </button>
{/if}

<style>
  .w a[href^="tel:"] {
    color: #fff;
    font-size: 14px;
    font-weight: 900;
    line-height: 24px;
  }
  .w {
    z-index: 1009;
    max-width: 45%;
    text-shadow: none;
    color: #fff;
    position: absolute;
    background: rgba(0, 0, 0, 0.7);
    border: 1px solid rgba(255, 255, 255, 0.5);
    bottom: 28px;
    left: 12px;
    fill: rgb(46, 46, 46);
    direction: ltr;
    cursor: pointer;
    padding: 2px 6px 2px 10px;
    outline: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: calc(max-content + 10px);
    width: auto;
    text-align: left;
  }

  @media (max-width: 500px) {
    .w {
      padding: 5px;
      height: auto;
    }
    .w a[href^="tel:"] {
      font-size: 10px;
    }
  }
</style>
