import type { VASTTracker } from "vast-client";
import { errorCodesVAST } from "../../../../shared/errors/error-codes-vast";
import { TerminateError } from "../../../../shared/errors/terminate-error";
import { log } from "../../../../shared/support/log";
import type { vpaidEvents } from "../../../../shared/types/vpaid/vpaid-callbacks";
import type { vpaidMethods } from "../../../../shared/types/vpaid/vpaid-methods";
import type { UiElements } from "../../methods/support/draw-ui";
import { errorStore } from "../../store/error-store";
import type { configVpaidI } from "../../vpaid-types";
import { callEvent } from "./../../methods/support/call-event";
import type { VPAID } from "./../../vpaid";
import { setUI } from "./set-ui";

/**
 * В AdLog событии мы можем принимать некоторые
 * управляющие комманды от принятого VPAID
 */
type controlleCommand = {
  /**
   * Отображать ли наши эллементы упавления
   */
  showUI: boolean;
};
interface listenEventsI {
  video: HTMLVideoElement;
  adUnit: vpaidMethods;
  vastTracker: VASTTracker;
  looped: configVpaidI["looped"];
  context: VPAID;
}
/**
 * Навешивает на события VPAID различные действия,
 * включая трекинг VAST urls
 */
export const listenEvents = (opt: listenEventsI): Partial<vpaidEvents> => {
  let isImpressed = false;
  const trackImpressedFn = () => {
    if (!isImpressed) {
      isImpressed = true;
      track.trackImpression();
      send("AdImpression");
    }
  };

  /**
   * Отсылает событие в вышестоящий плеер
   */
  const send = callEvent(opt.context);
  /**
   * Для трекинга событий в VAST
   */
  const track = opt.vastTracker;

  const events: Partial<vpaidEvents> = {
    AdLoaded: () => {
      log("VPAID COOKIE AdLoaded, sending adUnit.startAd()");
      opt.adUnit.startAd();
      send("AdLoaded");
    },
    AdStarted: () => {
      trackImpressedFn();
      send("AdStarted");
    },
    AdVolumeChange: () => {
      log("VPAID AdVolumeChange");
      opt.video.addEventListener("volumechange", (e) => {
        track.setMuted((<HTMLVideoElement>e.target).muted);
      });
      send("AdVolumeChange");
    },
    AdVideoStart: () => {
      log("VPAID AdVideoStart");
      trackImpressedFn();
    },

    AdPlaying: () => {
      log("VPAID COOKIE AdPlaying");
      try {
        /**
         * Попытаться установить звук в 0 при старте ролика, чтобы
         * обезопасить себя от инстрим контента
         */
        opt.adUnit.setAdVolume(0);
      } catch (error) {
        if (error instanceof Error) {
          log(`Set volume error  - ${error}`, true);
        }
        log(`Set volume error - common`);
      }
      track.setPaused(false);
      send("AdPlaying");
    },
    AdError: (e: Error | string) => {
      log("VPAID COOKIE AdErro");
      log(String(e), true);
      errorStore.set(new TerminateError(errorCodesVAST["VPAID AdError"]));
    },
    AdLog: (
      event: string | controlleCommand | { showUI: UiElements | boolean }
    ) => {
      log(`VPAID COOKIE AdLog`);
      if (typeof event === "string") {
        log(event);
        return send("AdLog", event);
      }
      /**
       * Кастомное событие для установки UI
       * сторонним VPAID
       */
      if (Object.prototype.hasOwnProperty.call(event, "showUI")) {
        setUI(event);
      }
    },
    AdImpression: () => {
      log("IMPRASSION VPAID COOKIE");
      isImpressed = true;
      track.trackImpression();
      send("AdImpression");
    },
    AdStopped: () => {
      log("VPAID COOKIE send STOPED");
      send("AdStopped");
    },
    AdVideoComplete: () => {
      log("VPAID COOKIE VIDEO COMPLETE");
      track.complete();
      send("AdVideoComplete");
    },
    AdUserClose: () => {
      log("VPAID COOKIE EVENT AdUserClose");
      track.complete();
      send("AdUserClose");
    },
    AdPaused: () => {
      log("VPAID COOKIE SEND AdPaused");
      track.setPaused(true);
      send("AdPaused");
    },
    AdClickThru: (url: string) => {
      log(`AdClickThru - ${url}`);
      track.click();
      send("AdClickThru");
    },
    AdVideoFirstQuartile: () => {
      log("VPAID COOKIE send AdVideoFirstQuartile");
      track.setProgress(25);
      send("AdVideoFirstQuartile");
    },
    AdVideoMidpoint: () => {
      log("VPAID COOKIE send AdVideoMidpoint");
      track.setProgress(50);
      send("AdVideoMidpoint");
    },
    AdVideoThirdQuartile: () => {
      log("VPAID COOKIE send AdVideoThirdQuartile");
      track.setProgress(75);
      send("AdVideoThirdQuartile");
    },
    AdSkipped: () => {
      log("VPAID COOKIE send AdSkipped");
      opt.context.skipAd();
      send("AdSkipped");
    },
  };

  return events;
};
