import type {
  adParamentrVpaidI,
  customAdParamsVpaidI,
} from "../../../../shared/types/vast/vast-ad";

export const instanceOfCookieMatching = (
  object: Partial<Record<keyof customAdParamsVpaidI, unknown>>
): object is adParamentrVpaidI => {
  if (object !== null && typeof object === "object") {
    return (
      "pixels" in object &&
      Array.isArray(object.pixels) &&
      "vast_link" in object &&
      typeof object.vast_link === "string"
    );
  }
  return false;
};
