import type { VastAd, VASTClient, VASTTracker } from "vast-client";
import type { customAdParamsVpaidI } from "../../shared/types/vast/vast-ad";
import type { vpaidEvents } from "../../shared/types/vpaid/vpaid-callbacks";
import { collapseAd } from "./methods/standart-vpaid/collapse-ad";
import { expandAd } from "./methods/standart-vpaid/expand-ad";
import { getAdAttribute } from "./methods/standart-vpaid/get-ad-attribute";
import { handshakeVersion } from "./methods/standart-vpaid/hand-shake-version";
import { initAd } from "./methods/standart-vpaid/init-ad";
import { pauseAd } from "./methods/standart-vpaid/pause-ad";
import { resizeAd } from "./methods/standart-vpaid/resize-ad";
import { resumeAd } from "./methods/standart-vpaid/resume-ad";
import { setAdVolume } from "./methods/standart-vpaid/set-volume-ad";
import { skipAd } from "./methods/standart-vpaid/skip-ad";
import { startAd } from "./methods/standart-vpaid/start-ad";
import { stopAd } from "./methods/standart-vpaid/stop-ad";
import { subscribe } from "./methods/standart-vpaid/subscribe";
import { unsubscribe } from "./methods/standart-vpaid/unsubscribe";
import type { attributesI, quartileEvents } from "./vpaid-types";

export class VPAID {
  /**
   * Div element на основной странице отданный под
   * управление VPAID
   */
  slot: null | HTMLDivElement = null;

  /**
   * video element на основной странице отданный под
   * рендер видео контента VPAID
   */
  videoSlot: null | HTMLVideoElement = null;

  eventsCallbacks: vpaidEvents = {} as vpaidEvents;

  /**
   * Если был принят васт сюда попадет последний не враппер
   */
  ads = {} as VastAd;
  /**
   * VPAID креатив ли принят
   */
  containVPAID = false;
  vastClient?: VASTClient;

  /**
   * Убран ли VPAID из DOM
   */
  isDestroyed = false;

  /** Атрибуты кампании */
  attributes: attributesI = {
    companions: "",
    desiredBitrate: 256,
    duration: 30,
    expanded: false,
    height: 0,
    icons: "",
    linear: true,
    remainingTime: 10,
    skippableState: false,
    viewMode: "normal",
    width: 0,
    volume: 1.0,
  };

  /**
   * Трекер для принятого васта
   */
  tracker?: VASTTracker;

  /**
   * Какие квартили отстукивать
   */
  readonly quartileEvents: { event: quartileEvents; value: number }[] = [
    { event: "AdVideoStart", value: 0 },
    { event: "AdVideoFirstQuartile", value: 25 },
    { event: "AdVideoMidpoint", value: 50 },
    { event: "AdVideoThirdQuartile", value: 75 },
    { event: "AdVideoComplete", value: 100 },
  ];

  /** Индекс последнего отстучавшего квартиля */
  lastQuartileIndex = 0;

  /** Параметры из AdParameters */
  parameters: customAdParamsVpaidI = {
    overlay: "",
    videos: [{ url: "", mimetype: "" }],
    vast_link: "",
  };

  /**
   *  Standart VPAID methods
   */
  public handshakeVersion = handshakeVersion;

  public initAd = initAd(this);

  public startAd = startAd(this);

  public stopAd = stopAd(this);

  public skipAd = skipAd(this);

  public setAdVolume = setAdVolume(this);

  public resizeAd = resizeAd(this);

  public pauseAd = pauseAd(this);

  public resumeAd = resumeAd(this);

  public expandAd = expandAd(this);

  public collapseAd = collapseAd(this);

  public subscribe = subscribe(this);

  public unsubscribe = unsubscribe(this);

  /** Get attribute value */
  public getAdVolume = getAdAttribute(this, "volume");

  public getAdWidth = getAdAttribute(this, "width");

  public getAdExpanded = getAdAttribute(this, "expanded");

  public getAdSkippableState = getAdAttribute(this, "skippableState");

  public getAdHeight = getAdAttribute(this, "height");

  public getAdRemainingTime = getAdAttribute(this, "remainingTime");

  public getAdDuration = getAdAttribute(this, "duration");

  public getAdCompanions = getAdAttribute(this, "companions");

  public getAdIcons = getAdAttribute(this, "icons");

  public getAdLinear = getAdAttribute(this, "linear");
}
