import {
  VASTClient,
  type VastRequestOptions,
} from "../../../libs/vast-client.js";
import { getNextAds } from "../../../shared/vast/get-next-ads.js";
import { replaceMacro } from "../../../shared/vast/replace-macro";
import { setCurrentAds } from "../../../shared/vast/set-current-ads";

interface fetchVastI {
  url: string;
  widthAndHeight: [number, number];
}
/**
 * Получает и парсит полученный васт
 */
export const fetchVast = async (props: fetchVastI) => {
  /** Fetch and parse VAST documents */
  const vastClient = new VASTClient();

  const options: VastRequestOptions = {
    withCredentials: true,
    wrapperLimit: 7,
    /** Returns a Promise which either resolves with a VASTResponse
     * or rejects with an Error. The resolved VASTResponse
     * can contain either a single Ad or AdPod or all the remaining Ads
     * if all parameter is passed as true.
     * By default the fully parsed VASTResponse contains all the
     * Ads contained in the VAST resource. It's possible to get only
     * the first Ad or AdPod and then get the remaining ones on demand
     * by passing resolveAll: false in the options parameter. */
    resolveAll: false,
  };

  replaceMacro(vastClient, props.widthAndHeight);
  let curretnAds;
  try {
    console.log('fetchVast', await vastClient.get(props.url, options))
    curretnAds = setCurrentAds({
      restAds: await vastClient.get(props.url, options),
      vastClient,
    });
    return curretnAds;
  } catch (error) {
    console.log(curretnAds);
    console.log(error);
    return await getNextAds(vastClient);

    /**
     * Если получить васт не вышло
     */
    /* throw new GetVastError(); */
  }
};
