import type { VPAID } from '../../vpaid';
import { callEvent } from '../support/call-event';

/**
 * Resumes the ad.
 */
export const resumeAd = (context: VPAID) => (): void => {
  (context.videoSlot as HTMLVideoElement)
    .play()
    .then(() => {
      callEvent(context)('AdResumed');
    })
    .catch((error) => console.log(error));
};
