<script lang="ts">
  import { onMount } from "svelte";
  import type { VastCreativeLinear } from "vast-client";
  import { isTouchDevice } from "../../../shared/support/is-touch-device";
  import type { CurrentAds } from "../../../shared/types/vast/current-ads";
  import { clickAction } from "../methods/support/click-action";
  import { controlleStore } from "../store/controlle-store";
  import type { VPAID } from "../vpaid";

  export let currentAds = {} as CurrentAds;
  export let context = {} as VPAID;

  const isEngLang = /en/g.test(navigator.language);

  const creative = currentAds?.ads?.creatives[0] as VastCreativeLinear;
  const icon = creative?.icons[0]?.staticResource;
  const title = currentAds?.ads?.title
    ? currentAds?.ads?.title
    : isEngLang
    ? "Ads"
    : "Реклама";

  const isTouchDeviceConst = isTouchDevice();
  let opacity = isTouchDeviceConst ? 0.8 : 0.4;
  let isHover = false;

  onMount(() => {
    if (!isTouchDeviceConst) {
      context.slot?.addEventListener("mouseenter", () => {
        isHover = true;
        opacity = 1;
      });
      context.slot?.addEventListener("mouseleave", () => {
        isHover = false;
        setTimeout(() => {
          if (!isHover) opacity = 0.4;
        }, 3000);
      });
    }
  });
</script>

{#if $controlleStore.actionButton}
  <button
    style="background: rgba(255, 255, 255, {opacity});"
    class="w"
    on:click="{() => {
      clickAction(context);
    }}"
  >
    <div class="d">
      {#if icon}
        <img class="d--icon" src="{icon}" alt="ads-logo" />
      {/if}

      <div class="d-t" style="padding-left: {icon ? '10px' : '5px'};">
        <div class="d-t--label">
          {title}
        </div>
      </div>

      <div class="d--button">
        {isEngLang ? "Go" : "Перейти"}
      </div>
    </div>
  </button>
{/if}

