import { log } from "../../../../shared/support/log";
import type { environmentVarsI } from "../../../../shared/types/vpaid/enviroment-vars";
import type { VPAID } from "../../vpaid";
import { callEvent } from "../support/call-event";
import { fetchCreativeDataWithMatch } from "../support/fetch-creative-data-with-match";
import { removeAll } from "../support/remove-all";
import { timeUpdateHandler } from "../support/time-update-handler";
import { initAds } from "./../support/init-ads";
import { stopAd } from "./stop-ad";

export type viewModeT = "normal" | "thumbnail" | "fullscreen";

/**
 * Инициирует VPAID
 * Обновляет видео слот
 * Подписывается на событие timeupdate и ended
 * Трекает событие VPAID callEvent
 */
export const initAd =
  (context: VPAID) =>
  async (
    width: number,
    height: number,
    viewMode: viewModeT,
    desiredBitrate: number,
    creativeData?: { AdParameters: string },
    environmentVars?: environmentVarsI
  ) => {
    context.attributes.width = width;
    context.attributes.height = height;
    context.attributes.viewMode = viewMode;
    context.attributes.desiredBitrate = desiredBitrate;

    if (!environmentVars) {
      callEvent(context)("AdError", "slot and videoSlot must be define");
      throw new Error("slot and videoSlot must be define");
    }
    // slot and videoSlot are passed as part of the environmentVars
    context.slot = environmentVars.slot;
    context.videoSlot = environmentVars.videoSlot;

    if (creativeData) {
      try {
        await fetchCreativeDataWithMatch(context, creativeData);
        await initAds(context);
      } catch (error) {
        if (error instanceof Error) {
          log(error, true);
          callEvent(context)("AdError", error.message);
        } else {
          callEvent(context)("AdError", "fetch vast error");
        }

        /**
         * Удаляем инстанс VPAID если не смогли получить васт
         */
        removeAll(context)();
      }
    } else {
      log("Remove player");
      callEvent(context)("AdError", "no AdParameters");
      /**
       * Если нет AdParameters наш впейд не знает откуда брать креативы
       * и он ничего сделать не может
       */
      return removeAll(context)();
    }

    /**
     * Подписаться на событие timeupdate у video
     */
    context.videoSlot.addEventListener(
      "timeupdate",
      timeUpdateHandler(context),
      false
    );

    context.videoSlot.addEventListener("ended", stopAd(context), false);

    callEvent(context)("AdLoaded");
  };
