import { regFindDomen, regFindDomenNoHttp } from "./rex-exps";

/**
 * @param {string} fullURL - The full URL to be cut.
 * @param [cutHttp=false] - boolean - if true, the protocol will be cut off.
 * @returns The result of the function.
 */
export const domainCutter = (fullURL: string, cutHttp = false) => {
  const regExpSelect = cutHttp ? regFindDomenNoHttp : regFindDomen;
  /**
   * Результат обрезки только до домена.
   * Может быть null или массив с результатами
   * */
  const domainRegFindResult = regExpSelect.exec(fullURL);
  /**
   * Оставляет только часть с доменом,
   * обрезая все лишнее.
   * Например: https:\\test.ru
   */
  const onlyDomain = Array.isArray(domainRegFindResult)
    ? domainRegFindResult[1]
    : domainRegFindResult;

  return onlyDomain;
};
